.node {
    width: 20px;
    height: 20px;
    /* outline: 1px solid rgb(175, 216, 248); */
    outline: 1px solid rgb(191, 203, 236);
    display: table-cell;
  }

  .node-finish {
    background: url(../Images/icons8-filled-circle-64.png);
    background-size: contain;
  }
  
  .node-start {
    background: url(../Images/pyramid_img.png);
    background-size: contain;
  }

  .node-wall {
    background-color: rgb(62 77 116);
    outline: rgb(62 77 116);
  }

  .node-visited {
    animation-name: visitedAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      background-color: #1e2f97;
      border-radius: 100%;
    }
  
    50% {
      background-color: #1aa7ec;
    }
  
    75% {
      transform: scale(1.2);
      background-color: #4adede;
    }
  
    100% {
      transform: scale(1);
      background-color: #797df6c2;
    }
  }
  
  .node-shortest-path {
    animation-name: shortestPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes shortestPath {
    0% {
      transform: scale(0.6);
      background-color: rgb(243, 181, 10);
    }
  
    50% {
      transform: scale(1.2);
      background-color: rgb(235, 243, 10);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(235, 243, 10);
      border-radius: 5px;
    }
  }