.navbar-container{
    height: 90px;
    background: #3b485b;
    display: flex;
    flex-direction: column;
}

.navbar-left{
    display: flex;
    align-items: baseline;
}

.navbar-title{
    max-width: 80%;
    margin-top: 12px;
    font-size: 20px;
    font-family: system-ui;
    padding-left: 10px;
    color: white;
    font-weight: 400;
}

.navbar-node-button-container{
    margin-left: 25%;
}

.navbar-right{
    display: flex;
    margin-left: 40%;
    height: -webkit-fill-available;
    align-items: end;
}



.navbar-algorithm-button-container{
    display: flex;
    margin-left: 60%;
    position: relative;
}

.navbar-button{
    position: relative;
    width: 6rem;
    height: 2rem;
    outline: none;
    border: none;
    margin: 0px 5px;
    font-size: 14px;
    font-weight: 400;
    border-radius: 5px;
    background: #3b485b;
    color: white;
    cursor: pointer;
    padding-right: 30px;
}

.start_node:hover{
    background-color: rgba(65, 105, 225, 0.425);
}

.end_node:hover{
    background-color: rgba(65, 105, 225, 0.425);
}

.navbar-button-start-span{
    position: absolute;
    top: 7px;
    left: 61px;
    height: 19px;
    width: 17px;
    background: url(data:image/png;base64,);
    background-size: contain;
}

.navbar-button-end-span{
    position: absolute;
    top: 7px;
    left: 58px;
    height: 19px;
    width: 17px;
    background: url(../Images/icons8-filled-circle-64.png);
    background-size: contain;
}

.navbar-dijkstra{
    width: 130px;
    height: 40px;
    padding: 3px;
    padding-right: 0px;
    margin-bottom: 6px;
    background: royalblue;
    /* display: none; */
}

.navbar-dijkstra:hover{
    background-color: royalblue;
}

.navbar-reset{
    position: absolute;
    padding: 3px;
    padding-right: 0px;
    padding-left: 10px;
    width: 130px;
    height: 40px;
    background: royalblue;
    display: none;
}

.navbar-button-reset-span{
    background: url(../Images/icons8-rotate-left-30.png);
    background-size: contain;
    position: absolute;
    top: 12px;
    left: 27px;
    height: 17px;
    width: 17px;
}


